*{
  background: 'linear-gradient(45deg, #FE6B8B 30%, #e2bfad 90%)';
  font-family: 'Poppins', sans-serif;

}



::-webkit-scrollbar {
  width: 10px;
  position: absolute;
  padding: '0px 3px';
}
::-webkit-scrollbar-thumb{
  background-color: #d888af;
  width: 2px;
  border: 3px solid white;
}

::-webkit-scrollbar-track-piece {
  background-color: #fff;
  width: 10px;


}

